.poiInfo-container {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.poiInfo-header .poiInfo-title {
    text-align: center;
}
.poiInfo-header .poiInfo-title:first-letter {
    text-transform: uppercase;
}

.poiInfo-header .poiInfo-date {
    color: grey;
    text-align:center;
}

.poiInfo-cta {
    margin-top: 10px;
    border: 1px solid lightgray;
    border-radius: 5px;
    display: flex;
    justify-content: space-evenly;
}